<!-- Lynn -->
<script>
export default {
  name: "engineeringDetails-component",
  data() {
    return {
      proj_Detail: [], // 工程資料
      vendor_Detail: [], // 承攬商資料
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    async getProjDetail(prjNo) {
      const res = await this.$store.dispatch("getProjDetail", prjNo);
      const data = res.data.data;

      // 將明細寫入 proj_Detail, vendor_Detail
      this.proj_Detail = data.proj_Detail;
      this.vendor_Detail = data.vendor_Detail;
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得該筆明細
    const prjNo = this.$route.params.prjNo;
    this.getProjDetail(prjNo);
  },
};
</script>

<template src="./engineeringDetails.html"></template>
